.ItemImg{
    width: 500px;
    height: 500px;
    margin-right: 200px;
}
.details{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}
.item{
    display: flex;
}
.details>div>h2{
    margin-bottom: 50px;
}
.details>div>button{
    background: rgba(0,0,0,.07);
    text-align: center;
    width: 360px;
    height: 56px;
    border: none;
    border-radius: 12px;
    font-size: 20px;
    transition: 0.3s;
    margin-bottom: 30px;
}