img{
    width:350px;
    height: 330px;
}
span{
    word-spacing: 7px;
}
.product{
    padding: 30px;
    width: 420px;
    transition: 0.3s;
    box-sizing: border-box;
    margin: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.product:hover{
    box-shadow: 0px 0px 30px -15px rgba(34, 60, 80, 0.2);
    transition: 0.3s;
    cursor: pointer;
}
.link{
    border: none;
    text-align: left;
    background: none;
    font-size: 17px;
    color: #06c;
    padding: 0;
}
.prodInfo{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}