.navbar{
    width: 100%;
    height: 7vh;
    display: flex;
    box-sizing: border-box;
    padding-left: 5vw;
    padding-right: 5vw;
}
.shoping-cart{
    background:none;
    border: none;
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.shoping-cart>span{
    color: aliceblue;
    background: red;
    width: 25px;
    height: 25px;
    text-align: center;
    border-radius: 100%;
    position: absolute;
    margin-left: 35px;
}
.nav-line{
    width: 90%;
    margin-left: 5vw;
    margin-right: 5vw;
    border-bottom:1px solid #3333 ;
    margin-top: 40px;
    margin-bottom: 40px;
}
.Logo{
    color: #333;
    text-decoration: none;
    outline: none;   
    font-size:40px;
}
.Logo:hover{
    color: #333;
}