h1{
    font-size: 50px;
    margin-bottom: 30px;
}
.cart-products{
    display: flex;
    justify-content: space-between;
}
.cart-products>div{
    display: flex;
    flex-direction: column;
}
.cart-products>div>button:nth-child(4){
    background: rgba(0,0,0,.07);
    text-align: center;
    width: 360px;
    height: 56px;
    border: none;
    border-radius: 12px;
    font-size: 20px;
    transition: 0.3s;
    margin-bottom: 30px;
}
.cart-products>div>button:nth-child(4):hover{
    background: rgba(0,0,0,.05);
    transition: 0.3s;
}
.back{
    border: none;
    text-align: left;
    background: none;
    font-size: 17px;
    color: #06c;
}
h3{
    margin-bottom: 140px;
}