img{
    width:380px;
    height: 360px;
}
span{
    word-spacing: 7px;
}
.CartItem{
    padding: 30px;
    width: 420px;
    transition: 0.3s;
    box-sizing: border-box;
    margin: 30px;
}
.number{
    width: 120px;
    height: 40px;
    background: rgba(0,0,0,.07);
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 15px;
    padding-left: 10px;
    padding-right: 10px;
    
}
.number>button{
    background: none;
    border: none;
    text-align: center;
    width: 30px;
    height: 30px;
    font-size: 19px;
    margin-bottom:2px ;

}
.CartItem>div{
    display: flex;
    justify-content: space-between;
}
h5{
    margin: 0;
}
.back{
    border: none;
    text-align: left;
    background: none;
    font-size: 17px;
    color: #06c;
}
input::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button{
    -webkit-appearance: none;
}